:root {
  --body-bg: #fafafa;
  --body-color: #444;
  --body-font: 'Roboto', sans-serif;
  --gray: #7e7e7e;
  --gray-light: #dcdcdc;
  --dark: #000;
  --avatar-bg: #bdbdbd; }

.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px; }
  @media (min-width: 700px) {
    .container {
      max-width: 700px; } }

body {
  margin: 0;
  padding: 0;
  font-family: var(--body-font);
  background-color: var(--body-bg);
  color: var(--body-color); }

img {
  max-width: 100%; }

.mark {
  color: var(--gray); }

.animated-page-wrapper {
  position: absolute;
  top: 62px;
  left: 0;
  width: 100%;
  height: 100%; }

.fadeIn-appear {
  -webkit-transform: translateY(20px);
      -ms-transform: translateY(20px);
          transform: translateY(20px);
  opacity: 0; }
  .fadeIn-appear.fadeIn-appear-active {
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear; }

.fadeIn-enter {
  opacity: 0; }
  .fadeIn-enter.fadeIn-enter-active {
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    -webkit-transition: all 0.2s linear 0.4s;
    -o-transition: all 0.2s linear 0.4s;
    transition: all 0.2s linear 0.4s; }

.fadeIn-leave {
  opacity: 1;
  -webkit-transform: translateY(-20px);
      -ms-transform: translateY(-20px);
          transform: translateY(-20px); }
  .fadeIn-leave.fadeIn-leave-active {
    opacity: 0;
    -webkit-transform: translateX(-30px);
        -ms-transform: translateX(-30px);
            transform: translateX(-30px);
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear; }

.mx-auto {
  margin-left: auto;
  margin-right: auto; }

.ml-1 {
  margin-left: 0.5rem !important; }

.mt-5 {
  margin-top: 2rem; }

@media (min-width: 1200px) {
  .mt-5 {
    margin-top: 3rem; } }

.mb-2 {
  margin-bottom: 1rem; }

.text-center {
  text-align: center; }

.d-block {
  display: block !important; }

.big-avatar {
  width: 100px !important;
  height: 100px !important;
  background-color: var(--avatar-bg); }

.lg-switch img {
  max-width: 20px; }

.lg-switch .active img {
  -webkit-filter: drop-shadow(0 0 5px var(--dark));
          filter: drop-shadow(0 0 5px var(--dark)); }

.main-loader {
  width: auto;
  margin: 40px auto;
  text-indent: 0; }

.description {
  text-indent: 30px;
  line-height: 1.5rem; }

.skill {
  cursor: default;
  color: var(--gray); }
  .skill:not(:last-of-type)::after {
    content: ', '; }
  .skill:last-of-type::after {
    content: '. '; }

.contacts img {
  display: inline-block;
  max-width: 18px;
  margin-right: 3px;
  vertical-align: middle; }

.contacts span {
  vertical-align: middle; }

.block {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px; }
  .block h2 {
    margin-bottom: 10px;
    font-weight: 500;
    text-align: center; }

.date {
  position: relative;
  padding: 0 20px; }
  .date::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    display: block;
    width: 100%;
    height: 2px;
    margin-top: -1px;
    background-color: var(--gray-light); }
  .date svg {
    position: relative;
    max-width: 20px;
    display: inline-block;
    vertical-align: middle; }
  .date span {
    position: relative;
    display: inline-block;
    font-weight: normal;
    font-style: normal;
    line-height: 1;
    letter-spacing: normal;
    vertical-align: middle;
    background-color: var(--body-bg); }
  .date > span {
    padding: 0 10px; }
  @media (max-width: 480px) {
    .date span {
      font-size: 16px; }
    .date span {
      font-size: 12px; } }


  .tc {
    text-align: center;
  }

  .row-print {
    page-break-after: auto;
    -webkit-column-break-inside: avoid;
            break-inside: avoid;
  }

  @media print {
    @page {
      margin: 1cm 0;
      size: A4 portrait;
    }

    
    body {
      margin: 0 40pt;
      background: #fafafa;
    }



    .no-print {
      display: none !important;
    }

    .date span {
      background-color: #fff !important;
    }
  }



